.my-3 {
    font-family: cursive;
    text-align: center;
    font-weight: bold;
    color: rgb(0, 0, 0);
}

.form-control {
    font-family: cursive;
    font-size: 25px;
    font-weight: bold;
    border-radius: 15%;
    outline: 4px solid rgb(0, 0, 0);
}

.txtpreview {
    background-image: linear-gradient(to bottom right, rgba(230, 255, 2, 0.5), rgba(255, 0, 191, 0.5), rgba(5, 5, 255, 0.5), rgba(6, 126, 6, 0.5));
    
}

button {
    font-family: cursive;
    padding: 10px;
    outline: 3px solid rgb(0, 0, 0);
    border-radius: 15px;
    font-size: 17px;
    font-weight: bold;
}

.resetbtn {
    font-family: cursive;
    padding: 10px;
    outline: 3px solid rgb(0, 0, 0);
    border-radius: 15px;
    color: white;
    font-size: 17px;
    font-weight: bold;
    background-color: rgb(10, 116, 255);
}